import {mapMutations} from 'vuex';
import request from '../../utils/request.js';

export default {
	data() {
		return {
			shapecode: '',
			isRepair: false
		}
	},
	onLoad() {
		
	},
	methods: {
		...mapMutations(['upShapeCode','upJSId', 'upJSName']),
		sweepHandler: function() {
			const self = this;
			uni.scanCode({
				success(res) {
					self.shapecode = res.result;
					self.upShapeCode(self.shapecode);
					console.log(res);
				},
				fail(err) {
					wx.showToast({
						title: err.errMsg,
						icon: 'none'
					});
					console.log(err);
				}
			})
		},
		shapecodeFn(e) {
			this.shapecode = e.detail.value;
			this.upShapeCode(this.shapecode);
		},
		isEmpty() {
			if (this.shapecode === '') {
				uni.showToast({
					title: '请填写条形码',
					icon: 'none'
				});
				return false;
			}
			return true;
		},
		repair() {
			if (this.isEmpty()) {
				request('/api/auth/checkOrder', 'POST', {
					bar_id: this.shapecode,
				}).then(res => {
					this.isRepair = true;
					if (res.data.status === 201) {
						request('/api/auth/getOrder', 'POST', {
							bar_id: this.shapecode
						}).then(res => {
							this.upJSId(res.data[0].consignor_id);
							this.upJSName(res.data[0].username);
							uni.navigateTo({
								url: '../repair/repair'
							});
						});
					} else {
						uni.navigateTo({
							url: '../check/check'
						});
					}
				});
			}
		},
		noRepair() {
			if (this.isEmpty()) {
				request('/api/auth/checkOrder', 'POST', {
					bar_id: this.shapecode,
				}).then(res => {
					if (res.data.status !== 201) {
						uni.showToast({
							title: '请先清洗/修复商品...',
							icon: 'none'
						});
					} else {
						uni.navigateTo({
							url: '../checkOut/checkOut'
						});
					}
				});
			}
		},
		setScheme() {
			if (this.isEmpty()) {
				uni.navigateTo({
					url: '../setScheme/setScheme'
				})
			}
		},
		repairSuc() {
			if (this.isEmpty()) {
				request('/api/auth/checkOrder', 'POST', {
					bar_id: this.shapecode,
				}).then(res => {
					if (res.data.status !== 201) {
						uni.showToast({
							title: '请先清洗/修复商品...',
							icon: 'none'
						});
					} else {
						uni.navigateTo({
							url: '../repairSuc/repairSuc'
						});
					}
				});
			}
		},
		myHandler() {
			uni.navigateTo({
				url: '../myHandler/myHandler'
			})
		}
	}
}
